.currency {
	background-repeat: no-repeat;
	background-image: url(../img/ruble.svg);
	background-size: 7px 10px;
	background-position: 50%;
	width: 7px;
	height: 10px;
	display: inline-block;
	vertical-align: middle;
	transform: translateY(-1px);
}
.restaurant-block {
	background-color: #fff;
	padding: 25px 40px 0 40px;
	border: 1px solid $border-color;
	border-radius: 4px;
	margin-bottom: 40px;
	&__mobile {
		display: none;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
		&-logo {
			min-width: 75px;
			width: 75px;
			margin-right: 5px;
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;
			img {
				display: block;
				width: 100%;
			}
		}
		&-info {
			width: calc(100% - 83px);
		}
	}
	&__title-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid $border-color;
		margin-bottom: 20px;
		padding-bottom: 20px;
	}
	&__title {
		font-size: 24px;
		line-height: 24px;
		font-weight: bold;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
		flex: 1px;
		margin-bottom: 0;
		letter-spacing: -0.05em;
	}
	&__raiting {
		margin-top: 0;
		margin-right: 0;
		margin-left: 17px;
		width: 140px;
		.company-block__stars-masks {
			width: 140px;
		}
	}
	&__info {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		.company-block__options {
			width: 24%;
		}
		.company-block__option-name {
			margin-bottom: 12px;
		}
		.company-block__option-value {
			font-weight: bold;
			font-size: 14px;
		}
	}
	&__descr {
		margin-top: 13px;
		margin-bottom: 15px;
		font-size: 14px;
		line-height: 24px;
		word-wrap: break-word;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		overflow: hidden;
		max-height: 70px;
	}
	&__panel {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	&__tabs {
		font-size: 0;
		flex: 1;
		position: relative;
	}
	&__tab-link {
		text-decoration: none;
		text-transform: uppercase;
		font-weight: bold;
		font-size: 11px;
		display: inline-block;
		vertical-align: middle;
		padding: 10px 38px;
		color: #000;
		border-bottom: 2px solid transparent;
		position: relative;
		&:hover {
			text-decoration: none;
			color: #000;
		}
		&--mobile {
			display: none;
		}
	}
	&__favorites {
		position: relative;
		right: -21px;
		top: -3px;
		input {
			display: none;
		}
		label {
			background-repeat: no-repeat;
			background-image: url(../img/heart-empty.svg);
			background-size: 21px 19px;
			width: 21px;
			height: 19px;
			margin: 0;
			vertical-align: middle;
			&:hover {
				cursor: pointer;
			}
		}
		input:checked+label {
			background-image: url(../img/heart.svg);
		}
	}
}

.magic-line,
.magic-line2 {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100px;
	height: 2px;
	background-color: $button-color;
}