.slick-prev:before, .slick-next:before { content: ''; position: absolute; top: 0; left: 0; background-repeat: no-repeat; background-image: url(../img/arrow-right-w2.svg); background-size: 30px 57px; width: 30px; height: 57px; opacity: 0.8; }

.slick-prev:hover:before, .slick-next:hover:before { opacity: 1; }

.slick-prev:focus, .slick-next:focus { outline: 0; }

.slick-prev { left: -55px; }

.slick-prev:before { transform: rotate(-180deg); }

.slick-next { right: -55px; }

/* Dots */
.slick-dotted.slick-slider { margin-bottom: 30px; }

.slick-dots { position: absolute; bottom: -25px; list-style: none; display: block; text-align: center; padding: 0; margin: 0; width: 100%; }

.slick-dots li { position: relative; display: inline-block; height: 20px; width: 20px; margin: 0 5px; padding: 0; cursor: pointer; }

.slick-dots li button { border: 0; background: transparent; display: block; height: 20px; width: 20px; outline: none; line-height: 0px; font-size: 0px; color: transparent; padding: 5px; cursor: pointer; }

.slick-dots li button:hover, .slick-dots li button:focus { outline: none; }

.slick-dots li button:hover:before, .slick-dots li button:focus:before { opacity: 1; }

.slick-dots li button:before { position: absolute; top: 0; left: 0; content: "•"; width: 20px; height: 20px; font-family: "slick"; font-size: 6px; line-height: 20px; text-align: center; color: black; opacity: 0.25; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before { color: black; opacity: 0.75; }

/*кнопки начать/пропустить в форме геолокации прелендинга создаем сначала кнопку пропустить, затем делаем extend всех ее свойств в кнопку начать.*/
