.goods-info-slider .goods-block__tocart-btn { font-size: 18px; height: 38px; padding-left: 25px; padding-right: 25px; min-width: 136px; max-width: 100%; }

.goods-info-slider .goods-block__amount { height: 38px; min-width: 136px; max-width: 100%; }

.goods-info-slider .goods-block__amount--plus, .goods-info-slider .goods-block__amount--minus { height: 38px; width: 38px; }

.goods-info-slider .goods-block__amount--value { height: 38px; max-width: 38px; max-height: 38px; font-size: 18px; }

.goods-info-slider .restaurant-block__favorites label { width: 28px; height: 26px; background-size: 28px 26px; }

.goods-info-slider__title { text-align: center; font-size: 18px; font-weight: bold; margin-top: 10px; margin-bottom: 15px; text-overflow: ellipsis; overflow: hidden; max-height: 52px; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; }

.goods-info-slider__descr { font-size: 14px; margin-bottom: 20px; margin-top: 25px; word-break: break-all; }

@media (min-width: 520px) { .goods-catalog-mobile { padding-left: 10px; padding-right: 10px; }
  .goods-catalog-mobile__categories { width: 100%; transform: translateX(0); }
  .goods-catalog-mobile__item { width: 100%; transform: translateX(0); }
  .goods-img-slider, .goods-info-slider { width: 100%; transform: translateX(0); } }

@media (min-width: 425px) { .goods-info-slider .goods-popup__charac-select { max-width: 300px; } }

body { background-color: #f5f5f5 !important; }

hr { border: 0; display: block; height: 1px; background: #DBDBDB; }

.companies-page { padding: 0 56px; }

h5, h4, h3, h2, h1 { font-weight: normal; }

.modal-open { position: fixed; padding-right: 0 !important; }

@media (max-width: 575px) { .content { padding: 75px 0 30px 0; }
  .companies-page { padding: 0 15px; }
  .modal-open { overflow: hidden; padding-right: 0 !important; } }

.hide-before-load { visibility: hidden; }

a:focus { color: inherit; text-decoration: none; }

a[href^="tel"] { text-decoration: none; }

.owl-item { float: left; }
