.goods-page {
	overflow-x: hidden;
	box-sizing: border-box !important;
}
.goods-catalog-mobile {
  .goods-popup__charac-select{
    border-color: #aaa;
  }
	&__categories {
		font-size: 0;
		background-color: #fff;
		overflow-x: auto;
		margin-bottom: 10px;
			width: calc(100% + 20px);
		transform: translateX(-10px);
		&-wrap {
			white-space: nowrap;
		}
	}
	&__category {
		display: inline-block;
		vertical-align: middle;
		font-size: 12px;
		padding: 17px 10px;
		text-decoration: none;
    border: 0;
		color: #a0a0a0 !important;
		&.active {
      position: relative;
      z-index: 2;
			color: #000 !important;
			border-bottom: 1px solid $button-color;
			background-color: #f8f8f8;
		}
	}
	&__items {
		border-top: 1px solid $border-color;
	}
	&__item {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 10px;
		border-bottom: 1px solid $border-color;
		background-color: #fff;
		width: calc(100% + 20px);
		transform: translateX(-10px);
	}
	&__img {
		width: 110px;
		margin-right: 7px;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			display: block;
			height: auto;
			width: 100%;
		}
	}
	&__info {
		width: calc(100% - 117px);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		.goods-block__tocart-wrap {
			max-height: 36px;
		}
		.goods-block__tocart-btn {
			padding: 6px 7px;
			height: 36px;
			max-height: 36px;
			position: relative;
			min-width: 0;
			&:before {
				content: '';
				display: block;
				background-repeat: no-repeat;
				background-image: url(../img/cart2.svg);
				width: 23px;
				height: 24px;
			}
		}
		.goods-block__amount--plus,
		.goods-block__amount--minus {
			width: 36px;
			height: 36px;
			font-size: 16px;
			line-height: 16px;
		}
		.goods-block__amount--value {
			height: 36px;
			max-height: 36px;
			font-weight: 600;
			font-size: 14px;
			max-width: 35px;
		}
		.goods-block__amount {
			height: 36px;
			max-width: 100%;
		}
	}
	a {
		text-decoration: none;
		color: #000;
	}
	&__title {
    position: relative;
		font-size: 14px;
		line-height: 15px;
		font-weight: 600;
		margin-top: 0;
		margin-bottom: 10px;
		line-height: 14px;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		overflow: hidden;
    &::after{
      display: inline-block;
      content: ' ';
      position: absolute;
      bottom: -2px;
      height: 3px;
      left: 0;
      right: 0;
      background: white;
      z-index: 1
    }
	}
	&__descr {
		font-size: 13px;
		line-height: 15px;
		margin: 0;
		color: $int-el-txt-color;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		overflow: hidden;
		margin-bottom: 15px;
	}
	&__price-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	&__price {
		font-size: 14px;
		font-weight: 600;
		margin: 0;
		color: #000;
	}
	&__more {
		border-radius: 4px;
		height: 36px;
		width: 36px;
		position: relative;
		text-decoration: none;
		background-color: $button-color;
		&:before {
			content: '';
			display: block;
			background-repeat: no-repeat;
			background-image: url(../img/arrow-right-new.svg);
			background-size: 10px 22px;
			width: 10px;
			height: 22px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		&:hover {
			background-color: darken($button-color, 25%);
			text-decoration: none;
		}
	}
}

.goods-img-slider {
	width: calc(100% + 20px);
	transform: translateX(-10px);
	&__slide {
		&.slick-active {
			img {
//				padding-left: 3px;
//				padding-right: 3px;
			}
		}
		img {
			display: block;
			height: auto;
			width: 94%;
			margin-left: auto;
			margin-right: auto;
		}
	}
}

.goods-info-slider {
	width: calc(100% + 20px);
	transform: translateX(-10px);
	.currency {
		width: 11px;
		height: 14px;
		background-size: 11px 14px;
	}
	&__slide {
		padding-left: 10px;
		padding-right: 10px;
	}
	.restaurant-block__favorites {
		display: inline-block;
	}
	.goods-popup__charac-block {
		margin-bottom: 10px;
	}
	.goods-block__price {
		font-size: 18px;
	}
	.goods-popup__charac-name {
		font-size: 18px;
		font-weight: 400;
	}
	.goods-popup__charac-select {
		font-size: 18px;
		line-height: 18px;
		max-width: 135px;
	}
	.goods-block__tocart-wrap {
		max-height: 38px;
	}
	.goods-block__tocart-btn {
		font-size: 18px;
		height: 38px;
		padding-left: 25px;
		padding-right: 25px;
		min-width: 136px;
		max-width: 100%;
	}
	.goods-block__amount {
		height: 38px;
		min-width: 136px;
		max-width: 100%;
		&--plus,
		&--minus {
			height: 38px;
			width: 38px;
		}
		&--value {
			height: 38px;
			max-width: 38px;
			max-height: 38px;
			font-size: 18px;
		}
	}
	.restaurant-block__favorites label {
		width: 28px;
		height: 26px;
		background-size: 28px 26px;
	}
	&__title {
		text-align: center;
		font-size: 18px;
		font-weight: bold;
		margin-top: 10px;
		margin-bottom: 15px;
		text-overflow: ellipsis;
		overflow: hidden;
		max-height: 52px;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}
	&__descr {
		font-size: 14px;
		margin-bottom: 20px;
		margin-top: 25px;
		word-break: break-all;
	}
}



@media (min-width: 520px) {
	.goods-catalog-mobile {
		padding-left: 10px;
		padding-right: 10px;
		&__categories {
			width: 100%;
		transform: translateX(0);
		}
		&__item {
			width: 100%;
			transform: translateX(0);
		}
	}
	.goods-img-slider,
	.goods-info-slider {
		width: 100%;
		transform: translateX(0);
	}
}
	@media (min-width: 425px) {
	.goods-info-slider .goods-popup__charac-select {
		max-width: 300px;
	}
}
